    public onSubmit(params) {
        const parameters = { ...params.importForm };
        this.importService.importDocument(parameters);
    }

    public handleFileInput($event: any) {
        this.fileToUpload = $event.target.files.item(0);
        this.fileElement.nativeElement.style.color = 'mediumblue';
        // console.log(this.fileElement.nativeElement.value);
        // console.log("File upload");
        // console.log(this.fileToUpload);
    }

    public clear() {
        this.formGroup.reset();
        this.fileElement.nativeElement.value = '';
        this.fileElement.nativeElement.style.color = 'grey';
        this.fileToUpload = null;
        this.LOGGER.debug('Form cleared');
    }

    public submit() {
        const option = this.formGroup.value.type;
        const url = this.formGroup.value.url;

        console.log('option: ' + option);
        console.log('url: ' + url);

        if (option === 'url' && (url === '' || url === null)) {
            this.errorMessage = 'Please enter an url.';
            this.valid = false;
        } else if (option === 'file' && this.fileToUpload === null) {
            this.errorMessage = 'Please select a PDF file.';
            this.valid = false;
        } else {
            console.log('Submit');
            this.valid = true;

            const parameters = this.formGroup.value;
            parameters.file = this.fileToUpload;
            console.log(parameters.file);

            this.importService.importDocument(parameters);
        }
    }
