import { Injectable } from '@angular/core';
import { select, NgRedux } from '@angular-redux/store';
import { Observable } from 'rxjs';
import { ImportApiDataApiActions } from './api/import-api.actions';
import { AppState } from '../../store/model';

@Injectable({
    providedIn: 'root'
})
export class ImportService {
    @select(['importApi', 'data'])
    public result$: Observable<string>;

    @select(['importApi', 'loading'])
    public loading$: Observable<boolean>;

    @select(['importApi', 'success'])
    public success$: Observable<boolean>;

    @select(['importApi', 'error'])
    public error$: Observable<boolean>;

    constructor(private importActions: ImportApiDataApiActions, private store: NgRedux<AppState>) {}

    public importDocument(parameters): Observable<string> {
        this.importActions.loadData(parameters);
        return this.result$;
    }
}
